.p-footer {
  background: $base-color;
  padding-top: rem(80);
  padding-bottom: rem(40);
  @include mq(sp) {
    padding-top: rem(72);
  }
}

.p-footer__inner {
  max-width: rem(1120);
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding-left: $padding-sp;
  padding-right: $padding-sp;
  flex-direction: row;
  justify-content: space-between;
  @include mq(tab) {
    flex-direction: column;
    padding: 0 rem(32);
  }
}
.p-footer__title {
  font-family: $second-font-family;
  font-size: 32px;
  font-weight: bold;
  color: $white;
  text-align: center;
  line-height: 1;
  @include mq(sp) {
    font-size: 24px;
  }
}

.p-footer__btn {
  z-index: 9999;
  position: fixed;
  right: 0;
  bottom: 0;
}
.p-footer__btn img {
  border: rem(1) solid $white;
  padding: rem(16.5) rem(10.5);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  display: block;
  transition: 0.4s ease-in-out;
 }


.p-footer_nav ul {
  display: flex;
  @include mq(sp) {
    flex-direction: column;
    text-align: center;
    padding-top: rem(40);
  }
}
.p-footer_nav ul li {
  color: $white;
  padding-left: rem(32);
  @include mq(sp) {
    padding-top: rem(15);
  }
}

.p-footer__copylight {
  text-align: center;
  color: $white;
  padding-top: rem(40);
  @include mq(sp) {
    padding-bottom: rem(20);
  }
}
.p-footer_nav ul li {
  font-family: $second-font-family;
  font-weight: bold;
  font-size: rem(16);
  text-align: center;
  line-height: 1.35;
  padding-left: rem(30);
  padding-right: rem(30);
  border-left: 2px solid $base-background;
  transform: skewX(150deg);
  @include mq(sp) {
    transform: skewX(0);
    font-size: rem(18);
    border-left: none;
  }
}
.p-footer_nav ul li a:last-child {
  transform: skewX(-150deg);
  display: inline-block;
  @include mq(sp) {
    transform: skewX(0);
  }
}
.p-footer_nav ul li:hover {
  opacity: 0.7;
}

.p-footer__logo {
  width: rem(118);
  height: rem(68);
  @include mq(tab) {
    margin-bottom: 50px;
  }
}
.p-footer__icon {
  padding-top: 30px;
  text-align: center;
}
