.p-contact-top {
  padding-top: rem(40);
  background: $bg-white;
  @include mq(sp) {
    padding-top: 0;
  }
}
.p-contact-top__texts {
  text-align: center;
}
.p-contact-top__title {
  width: 100%;
  max-width: rem(700);
  font-weight: $font-weight-bold;
  display: inline-block;
  @include mq(sp) {
    font-size: rem(18);
  }
}
.p-contact-top__title span {
  display: inline-block;
}
.p-contact-top__buttons {
  display: flex;
  padding-top: 40px;
  flex-wrap: wrap;
  max-width: rem(700);
  margin: auto;
  justify-content: space-evenly;
  padding-bottom: rem(80);
}

.p-contact-top__button {
  width: 25%;
  @include mq(tab) {
    width: 50%;
  }
  @include mq(ssp) {
    width: 100%;
  }
}
.p-contact-top__button {
  padding-top: rem(30);
}
// @media screen and (max-width: 450px){
//   .p-contact-top__button{
//     width: 50%;
//   }
// }
.p-contact {
  margin-top: rem(120);
  padding-bottom: rem(100);
  @include mq(sp) {
    margin-top: rem(100);
  }
}

.p-contact__forms {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  max-width: rem(720);
  background: $blue;
  background: radial-gradient(
        circle at top left,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      left top / 23px 23px no-repeat,
    radial-gradient(
        circle at top right,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      right top / 23px 23px no-repeat,
    radial-gradient(
        circle at bottom left,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      left bottom / 23px 23px no-repeat,
    radial-gradient(
        circle at bottom right,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      right bottom / 23px 23px no-repeat,
    linear-gradient(90deg, transparent 20px, #ca4 23px) left top / 51% 3px
      no-repeat,
    linear-gradient(-90deg, transparent 20px, #ca4 23px) right top / 51% 3px
      no-repeat,
    linear-gradient(90deg, transparent 20px, #ca4 23px) left bottom / 51% 3px
      no-repeat,
    linear-gradient(-90deg, transparent 20px, #ca4 23px) right bottom / 51% 3px
      no-repeat,
    linear-gradient(180deg, transparent 20px, #ca4 23px) left top / 3px 51%
      no-repeat,
    linear-gradient(0deg, transparent 20px, #ca4 23px) left bottom / 3px 51%
      no-repeat,
    linear-gradient(180deg, transparent 20px, #ca4 23px) right top / 3px 51%
      no-repeat,
    linear-gradient(0deg, transparent 20px, #ca4 23px) right bottom / 3px 51%
      no-repeat !important;
  padding-top: rem(80);
  @include mq(sp) {
    max-width: rem(600);
  }
}

.p-contact__texts {
  padding-top: rem(50);
  text-align: center;
  padding-bottom: rem(50);
}

.p-contact__text {
  line-height: 1.75;
  padding-left: 7%;
  padding-right: 7%;
  text-align: center;
  text-align: left;
  @include mq(sp) {
  }
}
.p-contact__text span {
  // display: inline-block;
}
.passing-txt {
  text-align: center;

  padding-bottom: rem(20);
  color: $base-color;
  font-size: 36px;
  font-weight: $font-weight-base;
  @include mq(sp) {
    font-size: rem(24);
  }
}

.wpcf7-spinner::before {
  @include mq(sp) {
    display: none !important;
  }
}
.wpcf7-spinner {
  @include mq(sp) {
    display: none !important;
  }
}
.submit-btn {
  @include mq(sp) {
    text-align: center;
  }
}

.p-contact-top__tel-box {
  padding-top: rem(40);
}

.p-contact-top__tels {
  display: flex;
  justify-content: center;
}
.p-contact-top__tel {
  font-size: rem(24);
  @include mq(sp) {
    font-size: rem(20);
  }
}
.p-contact-top__tel a {
  text-decoration: underline;
  color: $base-color;
}
