.l-page-class {
  padding-top: rem(200);
  background: $light-gray;
  padding-bottom: rem(100);
  @include mq(sp) {
    padding-top: rem(100);
  }
}
.l-page-class__inner {
  max-width: rem(1200);
  margin: 0 auto;
  width: 100%;
  text-align: center;
  padding: 0 40px;
  @include mq(sp) {
    padding: 0 15px;
  }
}

.l-page-class__title {
  text-align: center;
  font-family: $second-font-family;
  font-size: rem(36);
  padding-bottom: rem(20);
  display: inline-block;
  position: relative;
  padding: 20px 40px;
  margin-bottom: 50px;
  background: $white;
}

.l-page-class__title::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: "";
  background: radial-gradient(
        circle at top left,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      left top / 23px 23px no-repeat,
    radial-gradient(
        circle at top right,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      right top / 23px 23px no-repeat,
    radial-gradient(
        circle at bottom left,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      left bottom / 23px 23px no-repeat,
    radial-gradient(
        circle at bottom right,
        transparent 20px,
        #ca4 20px,
        #ca4 23px,
        transparent 23px
      )
      right bottom / 23px 23px no-repeat,
    linear-gradient(90deg, transparent 20px, #ca4 23px) left top / 51% 3px
      no-repeat,
    linear-gradient(-90deg, transparent 20px, #ca4 23px) right top / 51% 3px
      no-repeat,
    linear-gradient(90deg, transparent 20px, #ca4 23px) left bottom / 51% 3px
      no-repeat,
    linear-gradient(-90deg, transparent 20px, #ca4 23px) right bottom / 51% 3px
      no-repeat,
    linear-gradient(180deg, transparent 20px, #ca4 23px) left top / 3px 51%
      no-repeat,
    linear-gradient(0deg, transparent 20px, #ca4 23px) left bottom / 3px 51%
      no-repeat,
    linear-gradient(180deg, transparent 20px, #ca4 23px) right top / 3px 51%
      no-repeat,
    linear-gradient(0deg, transparent 20px, #ca4 23px) right bottom / 3px 51%
      no-repeat;
}
.l-page-class__title::after {
  position: absolute;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  content: "";
  box-shadow: 0 0 0 1px #ca4;
}
.l-page-classes__title h1 {
  font-weight: $font-weight-base;
}
.classes__card-title {
  font-weight: $font-weight-base;
  display: inline-block;
  background: $base-color;
  color: $white;
  padding: 20px 40px;
  min-width: 300px;
  border-radius: 10px;
  font-weight: bold;
  background-color: $base-color;
  box-shadow: 5px 5px 0 $blue;
  transition: 0.5s;
  border-radius: 5px;
  font-family: $font-family-base;
}

.classes__card-title:hover {
  transition: 0.5s;
  text-decoration: none;
  background-color: $blue;
  box-shadow: 0 0 0;
  color: $base-color;
}

.l-page-class__subtitle {
  width: 70%;
  margin: 0 auto;
  margin-bottom: rem(50);
  position: relative;
  text-align: center;
  font-family: $font-family-base;
  font-size: rem(36);
  padding-bottom: rem(40);
  padding-top: rem(40);
  color: $base-color;
  font-weight: $font-weight-base;
  background: #20337f;
  background: $base-color;
  color: $yellow;
  box-shadow: 0 0 5px $base-color;
  background-image: linear-gradient(45deg, $white 10px, transparent 0),
    linear-gradient(-45deg, $white 10px, transparent 0),
    linear-gradient(135deg, $white 10px, transparent 0),
    linear-gradient(-135deg, $white 10px, transparent 0);
  @include mq(sp) {
    font-size: rem(30);
    width: 100%;
    max-width: rem(600);
    margin-bottom: rem(144);
  }
}
.l-page-class__subtitle::before {
  position: absolute;
  top: 1px;
  right: 1px;
  bottom: 1px;
  left: 1px;
  content: "";
  box-shadow: 0 0 0 5px $white;
  z-index: 5;
}
.l-page-class__subtitle::after {
  position: absolute;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  content: "";
  box-shadow: 0 0 0 1px $white;
}
.l-page-class__subtitle span {
  position: relative;
  color: $white;
  font-size: rem(24);
  @include mq(sp) {
    font-size: rem(18);
  }
}
span::before,
.l-page-class__subtitle span::after {
  content: "";
  position: absolute;
  // top: 50%;
  // display: inline-block;
  // width: 44px;
  // height: 2px;
  // background-color: $white;
}

.l-page-class__subtitle span::after {
  right: -37px;
  transform: rotate(-60deg);
}
.l-page-class__image {
  max-width: 700px;
  margin: 0 auto;
}

.l-page-class__figure {
  text-align: center;
  position: relative;
  width: 100%;
  margin: 0 auto;

  @include mq(sp) {
    width: 100%;
  }
}
.l-page-class__figure img {
  position: relative;
  box-shadow: 20px 20px 0 $gray;
  @include mq(sp) {
    box-shadow: 10px 10px 0 $gray;
  }
}

.l-page-class__time {
  font-size: rem(24);
  position: relative;
  text-align: left;
  border: 3px solid $gray;
  border-radius: 0 10px 10px 10px;
  display: inline-block;
  background: $white;
  color: $black;
  margin-bottom: rem(30);
  margin-top: rem(80);
  padding: 30px 50px 30px 70px;
  font-weight: $font-weight-base;
  @include mq(sp) {
    padding: 1rem 1.5rem 1rem 3rem;
    display: block;
    margin: 50px auto 30px;
    max-width: rem(600);
    font-size: rem(18);
  }
}

.l-page-class__teacher {
  position: absolute;
  top: -36px;
  left: -3px;
  padding: 0 1em;
  font-weight: $font-weight-bold;
  color: #fff;
  border-radius: 10px 10px 0 0;
  background: $gray;
  transition: all 0.5s;
}
.l-page-class__teacher:hover {
  background: $base-color;
  color: $white;
  transition: all 0.5s;
}
.l-page-class__teacher-1st:hover {
  background: $base-color;
  color: $white;
  transition: all 0.5s;
}
.l-page-class__teacher-1st {
  position: absolute;
  top: -73px;
  left: -3px;
  padding: 0 1em;
  font-weight: $font-weight-bold;
  color: #fff;
  border-radius: 10px 10px 0 0;
  background: $gray;
}
.l-page-class__time p {
  font-size: 22px;
}
.l-page-class__teacher span {
  font-weight: normal;
  color: $white;
  margin-right: rem(10);
}
.l-page-class__teacher-1st span {
  font-weight: normal;
  color: $white;
  margin-right: rem(10);
}
.l-page-class__figure:after {
  display: none;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 5px);
  width: 100%;
  height: calc(100% - 5px);
  height: 100%;
  border-image-slice: 20;
  border-image-repeat: round;
  border: 7px solid $white;
  @include mq(sp) {
    display: block;
  }
}
.l-page-class__figure:before {
  display: none;
  content: "";
  z-index: 2;
  background: url(../img/logo-gray.png) no-repeat center center / contain;
  right: 10px;
  bottom: 0;
  position: absolute;
  width: 100px;
  height: 100px;
  @include mq(sp) {
    width: 60px;
    height: 60px;
    display: block;
  }
}

.wp-block-image {
  margin: 0 auto 30px;
  width: 100%;
}
@media screen and (max-width: 768px) {
  .wp-block-image {
    overflow-x: auto;
    margin-bottom: 40px;
  }
}
.wp-block-image img {
  max-width: 100%;
  min-width: 750px;
  vertical-align: top;
}
.wp-block-image::-webkit-scrollbar {
  height: 4px;
}
/*スクロールバー全体の背景*/
.wp-block-image::-webkit-scrollbar-track {
  background: #eee;
}
/*スクロールバーの動く部分*/
.wp-block-image::-webkit-scrollbar-thumb {
  background: $orange;
  border: none;
}
/*スクロールバーの動く部分のホバー（マウスオーバー）*/
.wp-block-image::-webkit-scrollbar-thumb:hover {
  background: #999;
}
.p-overview {
  width: 80%;
  max-width: rem(800);
  margin: 80px auto 0;
  @include mq(sp) {
    width: 100%;
    max-width: rem(600);
  }
}
.p-overview__title {
  margin-top: 40px;

  position: relative;
  padding: 1.5rem;
  text-align: center;
  border: 2px solid #000;
  background: $base-color;
  color: $white;
}
.p-overview__wrapper-2nd {
  display: block;
}
.p-overview__wrapper-2nd dl {
  width: 100%;
  background: $white;
}
.p-overview__wrapper-2nd dt {
  background: $gray;
  padding: 5px;
  color: $white;
  font-weight: bold;
  border-right: 1px solid $gray;
  border-bottom: 1px solid $gray;
}
.p-overview__wrapper {
  display: flex;
  width: 100%;
  border: 1px solid $gray;
  @include mq(sp) {
    display: block;
  }
}
.p-overview__wrapper dl {
  width: 30%;
  background: $white;
  @include mq(sp) {
    width: 100%;
  }
}
.p-overview__wrapper dt {
  background: $gray;
  padding: 5px;
  color: $white;
  font-weight: bold;
  border-right: 1px solid $gray;
  border-bottom: 1px solid $gray;
}
.p-overview__wrapper dd:last-child {
  border-bottom: 0;
}
.p-overview__wrapper dd {
  background: #fff;
  padding: 20px 5px;
  border-bottom: 1px solid $gray;
  color: $black;
}
.p-overview__maps {
