.l-page-class__contact {
  padding-top: rem(80);
}
.l-page-class__text {
  margin-bottom: 30px;
  position: relative;
  font-size: rem(24);
  color: $black;
  display: inline-block;
  padding: 0px 20px;
  margin: auto;
  @include mq(sp) {
    font-size: rem(18);
    width: 100%;
    max-width: rem(600);
  }
}
.l-page-class__text::before,
.l-page-class__text::after {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  content: "";
  background: $base-color;
}
.l-page-class__text::before {
  top: -5px;
}
.l-page-class__text::after {
  bottom: -5px;
}
.l-page-class__text a {
  margin-top: rem(30);
}

.p-btn_wrapper {
  display: flex;
  justify-content: center;
  @include mq(sp) {
    display: block;
  }
}
.p-btn_wrapper--2nd {
  padding-bottom: rem(30);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: auto;
}
.p-btn_wrapper--3rd {
  display: flex;
  justify-content: center;
  @include mq(sp) {
    display: block;
  }
}

.l-page-class__text-2nd {
  padding-top: rem(15);
  padding-bottom: rem(15);
}
.l-page-class__text-2nd p {
  display: inline-block;
  text-decoration: underline double #ff99ab;
}
.p-class__btn-totsuka {
  margin-top: rem(80);
  @include mq(sp) {
    margin-top: rem(40);
  }
}
.p-class__scroll {
  display: none;
  color: $orange;
  font-weight: bold;
}
@media screen and (max-width: 768px) {
  .p-class__scroll {
    display: block;
  }
}
.l-page-class__times {
  margin-bottom: 80px;
  margin-bottom: 60px;
  @include mq(sp) {
    margin-bottom: rem(40);
  }
}

.p-btn__title--2nd {
  margin-top: 10px;
  margin-bottom: rem(10);
  color: $orange;
  padding: rem(10) rem(15);
  background: $white;
  display: inline-block;
  border-radius: 20px;
}
.p-btn__title--3rd {
  margin-top: rem(10);
  margin-bottom: rem(10);
  padding: rem(10) rem(15);
  background: $white;
  display: inline-block;
  border-radius: 20px;
  color: $base-color;
}

.l-page-class__contacts {
  max-width: rem(800);
  width: 100%;
  margin: auto;
  padding: 0 40px;
  @include mq(sp) {
    padding: 0;
  }
}
.l-page-class img {
  max-width: 500px;
  width: 100%;
}

.current-item br {
  display: none;
}
.current-item {
  display: inline-block;
}
