.c-top-btn{
    width: rem(45); 
    height: rem(45);
    display: block;
    background-color: $base-color;
    text-decoration: none;
    opacity: 1;
    position: relative;
 }

.c-top-btn__white{
    padding: rem(16.5) rem(10.5);  
    position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	display: block;
	transition: .4s ease-in-out;
}

